import React, { useState, useEffect } from 'react';
import ImageCarousel from '../../component/imagecarousel/ImageCarousel';
import { getOther } from '../../redux/actions/app'
import Zoom from 'react-reveal/Zoom';
import { connect } from 'react-redux'

const Main = React.memo(
    (props) =>{
        let [virtual, setVirtual] = useState([]);
        const { lang } = props.app;
        useEffect(async ()=>{
            let data = await getOther({name: 'Журналы'})
            await setVirtual(data)
        },[])
        return (
            <>
            <br/>
            <br/>
            <div className='textlinebig'>{lang===undefined||lang==='ru'? 'Журналы' : lang==='kg'? 'Журналы' : 'Magazines'}</div>
                <br/>
                <br/>
                {
                    virtual.length>0?(virtual).map((element) => {
                        return <>
                        <Zoom>
                            <div>
                                <a href={element.photos} target='_blank' className='link' style={{fontSize: '16px', fontWeight: 'bold'}}>{element.name}</a>
                            </div>
                        </Zoom>
                        <br/>
                        <br/>
                        </>
                    }):null
                }
            </>
        );
    })

function mapStateToProps (state) {
    return {
        app: state.app
    }
}

export default connect(mapStateToProps)(Main);
